import { CangjieInputEvent, Controller } from '@ali/4ever-cangjie';
import { ImagePluginConfig, ImageState } from '../types';
import onSpace from './onSpace';

export default function createOnCangjieInput(config: ImagePluginConfig, state: ImageState) {
  return function onCangjieInput(
    event: CangjieInputEvent,
    controller: Controller,
    next: Function,
  ) {
    if (event.detail.data !== ' ')
      return next();

    if (!config.disableMarkdown) {
      return onSpace(event, controller, next);
    }

    return next();
  }
}
